import React, { useState } from "react";
import { Table, Button, Modal, Form } from "react-bootstrap";

const ProductPage = () => {
  const [products, setProducts] = useState([
    { id: 1, name: "Laptop", price: "$1200", category: "Electronics", status: "Active" },
    { id: 2, name: "Novel", price: "$20", category: "Books", status: "Inactive" },
  ]);
  const [showModal, setShowModal] = useState(false);
  const [newProduct, setNewProduct] = useState({});
  const [isEditing, setIsEditing] = useState(false);

  const categories = ["Electronics", "Books", "Clothing"];

  const handleShowModal = (product = null) => {
    setNewProduct(product || {});
    setIsEditing(!!product);
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setNewProduct({});
  };

  const handleFormSubmit = () => {
    if (isEditing) {
      setProducts((prev) =>
        prev.map((p) => (p.id === newProduct.id ? newProduct : p))
      );
    } else {
      setProducts((prev) => [
        ...prev,
        { ...newProduct, id: prev.length + 1 },
      ]);
    }
    handleCloseModal();
  };

  const handleDelete = (id) => {
    setProducts((prev) => prev.filter((p) => p.id !== id));
  };

  return (
    <div className="container">
      <h1 className="pageTitle">Product Page</h1>

      {/* Add Product Button */}
      <div className="buttonGroup">
        <Button onClick={() => handleShowModal()}>Add Product</Button>
      </div>

      {/* Product Table */}
      <Table bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>Name</th>
            <th>Price</th>
            <th>Category</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {products.map((product) => (
            <tr key={product.id}>
              <td>{product.id}</td>
              <td>{product.name}</td>
              <td>{product.price}</td>
              <td>{product.category}</td>
              <td>
                <span
                  className={`statusBadge ${
                    product.status === "Active" ? "active" : "inactive"
                  }`}
                >
                  {product.status}
                </span>
              </td>
              <td>
                <Button
                  variant="info"
                  size="sm"
                  onClick={() => handleShowModal(product)}
                >
                  Edit
                </Button>{" "}
                <Button
                  variant="danger"
                  size="sm"
                  onClick={() => handleDelete(product.id)}
                >
                  Delete
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {/* Modal Form */}
      <Modal show={showModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>{isEditing ? "Edit Product" : "Add Product"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form className="modalForm">
            <Form.Control
              type="text"
              placeholder="Name"
              value={newProduct.name || ""}
              onChange={(e) =>
                setNewProduct({ ...newProduct, name: e.target.value })
              }
            />
            <Form.Control
              type="text"
              placeholder="Price"
              value={newProduct.price || ""}
              onChange={(e) =>
                setNewProduct({ ...newProduct, price: e.target.value })
              }
            />
            <Form.Select
              value={newProduct.category || ""}
              onChange={(e) =>
                setNewProduct({ ...newProduct, category: e.target.value })
              }
            >
              <option value="">Select Category</option>
              {categories.map((category) => (
                <option key={category} value={category}>
                  {category}
                </option>
              ))}
            </Form.Select>
            <Form.Select
              value={newProduct.status || ""}
              onChange={(e) =>
                setNewProduct({ ...newProduct, status: e.target.value })
              }
            >
              <option value="Active">Active</option>
              <option value="Inactive">Inactive</option>
            </Form.Select>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Close
          </Button>
          <Button variant="primary" onClick={handleFormSubmit}>
            {isEditing ? "Update" : "Add"}
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default ProductPage;
